.note.new {
    background-color: #67d7cc;
}

textarea {
    border: none;
    resize: none;
    background-color: #67d7cc;
}

textarea:focus {
    outline: none;
}

.save {
    background-color: #e1e1e1;
    border: none;
    border-radius: 15px;
    padding: 5px 10px;
    font-weight: bold;
}

.save:hover {
    background-color: #ededed;
    cursor: pointer;
}

.dark-mode .note.new {
    background-color: #48968e;
}

.dark-mode textarea {
    background-color: #48968e;
    color: #fff;
}